import { useState } from 'react';
import firebase from '../../firebase.config';
import scanSound from '../../assets/sounds/scanner.mp3';
import Header from '../../components/Header/Header';
import Title from '../../components/Title/Title';
import ScanArea from '../../components/ScanArea/ScanArea';

const ScanQRcode = () => {
  const [result, setResult] = useState('');
  const [audio] = useState(new Audio(scanSound));

  const handleScan = (data) => {
    if (data) {
      firebase
        .firestore()
        .collection("parkirIn")
        .doc('qrcode-scan')
        .set({id_qrcode: data})
        .then(() => {
          audio.play().then(() => console.log('ID Parking didapat.'));
          setResult(data);
          setTimeout(() => {
            audio.pause();
          }, 1100);
        })
        .catch((err) => console.log(err));
    }
  }

  const handleError = err => {
    console.error(err)
  }

  return (
    <div className="main">
      <Header />
      <Title />
      <ScanArea 
        onError={handleError}
        onScan={handleScan} />
      <div className="title">
        <p>Parking ID: {result}</p>
      </div>
    </div>
  );

}

export default ScanQRcode;