import QrReader from 'react-qr-reader';

export default function ScanArea(props) {
  return (
    <div className="scan-area">
      <QrReader
        delay={100}
        facingMode="environment"
        onError={props.onError}
        onScan={props.onScan}
        className="qrscanner"
        style={{ width: '100%' }} />
      <div className="diode">
        <div className="laser"></div>
      </div>
    </div>
  );
}